.pickerview-container {
	z-index: 9;
}

.dater {
	min-width: 30px;
	height: 24px;
	line-height: 24px;
	text-indent: 8px;
	border: solid 1px #ccc;
	background-color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.dater span {
	font-size: 14px;
}

.dater img {
	display: block;
	width: 16px;
	height: 16px;
	margin-right: 10px;
}
